
import { defineComponent } from "vue";
import CustomerInformation from "@/shared/components/CustomerInformation/CustomerInformation.vue";
import { modalController } from "@ionic/vue";
import { IBSubscriptionList, CIB20100111_BODY, CIB20100111_RES, CHECKEDLIST } from "@/shared/type/CIB20100111.type";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import BreadCrumb from "@/shared/components/BreadCrumb/BreadCrumb.vue";
import { CIB20100114_REQ_BODY, CIB20100114_RES, CustomerInfoInquiryDetail, } from "@/shared/type/CIB20100114.type";
import { APM20200113_RES } from '@/shared/type/APM20200113.type';
import { screenMode } from '@/shared/type/Approval.type';

export default defineComponent({
  name: "cib203001sc00",
  components: {
    CustomerInformation,
    Pagination,
    BreadCrumb,
  },

  data() {
    const searchFields = {
      customerNo: '',
      nationalID: "",
      passportNumber: "",
      businessNo: "",
      customerFullName: "",
    };
    const headers = [
      { columnName: "IB User ID" },
      { columnName: "Customer Type" },
      { columnName: "Status" },
      { columnName: "Open Date" },
      { columnName: "Close Date" },
    ];

    return {
      headers,
      dataTable: [] as IBSubscriptionList[],
      searchFields,
      checkedList: [] as Array<CHECKEDLIST>,
      checked: false,
      isAllChecked: false,
      customerInfo: {} as CustomerInfoInquiryDetail,
      isInValidInput: false,
      totalItems: 0,
      currentPage: 1,
      pageSize: PAGE_SIZE,
      startingIndex: 1,
      resetPage: false,
      approvalRes: {} as any,
      operationMode: 'normal' as screenMode
    };
  },

  async created() {
    if (this.approvalType === '') {
      if (this.searchFields.customerNo) {
        await this.handleSearch();
      }
    } else {
      await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {

        let authDetailParsed = JSON.parse(authDetail.body.requestContent)

        this.approvalRes = authDetail.body
        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal';

        authDetailParsed.list.find((item: CustomerInfoInquiryDetail) => {
          this.searchFields.customerNo = item.customerNo
        })

        const reqBody: CIB20100114_REQ_BODY = {
          customerNo: this.searchFields.customerNo,
        };

        const resBody = (await this.$request.getRequest("CIB20100114", reqBody, false)) as CIB20100114_RES;
        if (resBody.header.result) {
          this.customerInfo = resBody.body
          this.dataTable = authDetailParsed.list
        }
      })
    }
  },

  computed: {
    approvalType(): string {
      return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
    },
  },

  methods: {
    async getCustomerInformation() {
      const reqBody: CIB20100114_REQ_BODY = {
        customerNo: this.searchFields.customerNo,
      };

      const resBody = (await this.$request.getRequest( "CIB20100114", reqBody, false )) as CIB20100114_RES;

      if (resBody.header.result) {
        this.customerInfo = {
          customerNo: resBody.body.customerNo,
          customerType: resBody.body.customerType,
          localCustomerName: resBody.body.localCustomerName,
          customerShortName: resBody.body.customerShortName,
          countryCode: resBody.body.countryCode,
          nationality: resBody.body.nationality,
          branchCode: resBody.body.branchCode,
          branchName: resBody.body.branchName,
          customerFullName: resBody.body.customerFullName,
          location: resBody.body.location,
          mailersRequiredYN: resBody.body.mailersRequiredYN,
          customerStatusCode: resBody.body.customerStatusCode,
          customerFirstName: resBody.body.customerFirstName,
          customerLastName: resBody.body.customerLastName,
          dateOfBirth: resBody.body.dateOfBirth,
          genderCode: resBody.body.genderCode,
          nationalID: resBody.body.nationalID,
          passportNumber: resBody.body.passportNumber,
          iDExpiryDate: resBody.body.iDExpiryDate,
          consumerPhone: resBody.body.consumerPhone,
          email: resBody.body.email,
          customerAddress: resBody.body.customerAddress,
        }

      } else {
        this.customerInfo = {
          customerNo: "",
          customerType: "",
          localCustomerName: "",
          customerShortName: "",
          countryCode: "",
          nationality: "",
          branchCode: "",
          branchName: "",
          consumerPhone: "",
          customerFullName: "",
          customerAddress: "",
          location: "",
          mailersRequiredYN: "",
          customerStatusCode: "",
          customerFirstName: "",
          customerLastName: "",
          dateOfBirth: "",
          genderCode: "",
          nationalID: "",
          passportNumber: "",
          iDExpiryDate: "",
          email: "",
        };
      }
    },

    async handleSearch(mode?: string) {
      if (!this.searchFields.customerNo) {
        return (this.isInValidInput = true);
      }

      const tableBody: CIB20100111_BODY = {
        customerNo: this.searchFields.customerNo,
        pageSize: this.pageSize,
        pageNumber: mode == 'search' ? 0 : this.currentPage,
      };

      const data = (await this.$request.getRequest("CIB20100111", tableBody, false)) as CIB20100111_RES;

      if (mode === 'search') {
        this.currentPage = 1
        this.resetPage = true;
      }

      if (data.header.result) {
        this.dataTable = JSON.parse(JSON.stringify(this.convertCodeToValue(data.body.list)))
        this.totalItems = data.body.totalCount;
      }

      this.getCustomerInformation();
    },

    convertCodeToValue(mapdata: IBSubscriptionList[]): IBSubscriptionList[] {
      return mapdata.map((data: IBSubscriptionList) => {
        return {
          accountManagementTypeCode: data.accountManagementTypeCode,
          allowAccountOpenCloseYN: data.allowAccountOpenCloseYN,
          applicationDate: this.$datetime.formatStringToStringDate(data.applicationDate),
          authorizationLineAssignTypeCode: data.authorizationLineAssignTypeCode,
          authorizationLinePolicyTypeCode: data.authorizationLinePolicyTypeCode,
          cellPhoneNo: data.cellPhoneNo,
          cellPhoneNo2: data.cellPhoneNo2,
          companyName: data.companyName,
          customerCorpIndTypeCode: this.$codeConverter.convertValue('CustomerCorpIndTypeCode', data.customerCorpIndTypeCode),
          customerName: data.customerName,
          customerNo: data.customerNo,
          email: data.email,
          feeExemptYN: data.feeExemptYN,
          feeServiceStatusCode: data.feeServiceStatusCode,
          feeWithdrawalAccountNo: data.feeWithdrawalAccountNo,
          languageCode: data.languageCode,
          mainTransferAccount: data.mainTransferAccount,
          mainUserRoleID: data.mainUserRoleID,
          managerBankID: data.managerBankID,
          mobileISDCode: data.mobileISDCode,
          mobileISDCode2: data.mobileISDCode2,
          securityMediaNo: data.securityMediaNo,
          securityMediaTypeCode: this.$codeConverter.convertValue( "SecurityMediaTypeCode", data.securityMediaTypeCode),
          serviceStatusCode: data.serviceStatusCode,
          subscriptionApprovalTypeCode: data.subscriptionApprovalTypeCode,
          terminationDate: this.$datetime.formatStringToStringDate(data.terminationDate),
          userID: data.userID,
          userName: data.userName,
        }
      })
    },

    resetForm() {
      this.searchFields.customerNo = "";
      this.searchFields.nationalID = "";
      this.searchFields.passportNumber = "";
      this.searchFields.businessNo = "";
      this.searchFields.customerFullName = "";
      this.isInValidInput = false;
      this.currentPage = 1;
      this.resetPage = true;
      this.totalItems = 0;
      this.customerInfo = {} as CustomerInfoInquiryDetail;
      this.dataTable = [] as IBSubscriptionList[];
    },

    handleChecked(item: IBSubscriptionList) {
      if (item.checked) {
        this.checkedList.push({
          customerNo: item.customerNo,
          userID: item.userID,
          serviceStatusCode: item.serviceStatusCode
        });
      } else {
        const index = this.checkedList.findIndex(
          (e) => e.userID == item.userID
        );

        if (index !== -1) {
          this.checkedList.splice(index, 1);
        }
      }

      this.isAllChecked = this.checkedList.length === this.dataTable.length
    },

    handleAllChecked() {
      if (this.isAllChecked) {
        this.dataTable.forEach((item: IBSubscriptionList) => {
          return (item.checked = true);
        });
      } else {
        if (this.dataTable.length === this.checkedList.length) {
          this.dataTable.forEach((item: IBSubscriptionList) => {
            return (item.checked = false);
          });
        }
      }
    },

    handlePagination(pageNumber: number) {
      this.currentPage = pageNumber;
      this.resetPage = false;
      this.handleSearch();
    },

    handleStartingIndex(startingIndex: number) {
      this.startingIndex = startingIndex;
    },

    async unsubscribed() {
      const body = {
        list: this.checkedList,
      };

      const data = (await this.$request.getRequest( "CIB20300131", body, false )) as CIB20100111_RES;

      if (data.header.result) {
        this.$dialog.onOpen({
          content: this.$t("cib203001sc00.unsubscribe_successfully"),
          dialogMode: "Success",
          callback: () => {
            this.handleSearch();
            modalController.dismiss();
          },
        });
      }

      return data.header.result;
    },

    handleUnsubscribe() {
      const statusCode = "02"

      if (!this.checkedList.length) {
        return this.$dialog.onOpen({
          content: this.$t("cib203001sc00.please_select_one_to_suspend"),
          dialogMode: "Warning",
          callback: () => {
            modalController.dismiss();
          },
        });
      }

      let userID = "";
      this.checkedList.forEach((user) => {
        if (user.serviceStatusCode === statusCode) {
          return (userID = user.userID);
        }
      });

      if (userID) {
        return this.$dialog.onOpen({
          content: `${this.$t( "cib203001sc00.this_ib_user" )} ${userID} ${this.$t("cib203001sc00.is_already_terminated")}`,
          dialogMode: "Warning",
          callback: () => {
            modalController.dismiss();
          },
        });
      }

      this.checkedList.forEach((user) => {
        if (user.serviceStatusCode !== statusCode) {
          return (userID = user.userID);
        }
      });

      return this.$dialog.onOpen({
        content: `${this.$t( "cib203001sc00.do_you_want_to_unsubscribe_this_ib_user" )} ${userID}?`,
        dialogMode: "Question",
        callback: () => {
          this.unsubscribed().then((res) => {
            if (res) {
              this.handleSearch();
              modalController.dismiss();
            }
          });
          modalController.dismiss();
        },
      });
    },
  },
});
